.tippy-tooltip {
  box-sizing: border-box;
  color: #000;
  background-color: #fff;
  border: 1px solid #dcdcdc;
  border-radius: 4px;
  font-size: 12px;
  line-height: 13px;
  box-shadow: 0 0 5px #00000040;
  max-width: 240px !important;
}

.tippy-content {
  padding: 7.5px 10px 8.5px;
}

.tooltip-content .tooltip-title {
  margin: 0 0 8.5px;
  font-family: GT America Bold;
  font-weight: 400;
}

.tooltip-content .tooltip-table {
  table-layout: fixed;
  border-collapse: collapse;
  width: 100%;
}

.tooltip-content .tooltip-table tbody tr {
  border-top: .5px solid #d6d6d6;
}

.tooltip-content .tooltip-table th {
  text-align: right;
  text-transform: uppercase;
  vertical-align: bottom;
  width: 60px;
  padding: 0 5px 5px;
  font-family: GT America Condensed;
  font-weight: 400;
}

.tooltip-content .tooltip-table th:empty {
  padding: 0;
}

.tooltip-content .tooltip-table td {
  text-align: right;
  text-transform: capitalize;
  font-variant-numeric: tabular-nums;
  width: 60px;
  padding: 5px;
}

.tooltip-content .tooltip-table td:first-child {
  text-align: left;
  padding-left: 0;
}

.tooltip-content .tooltip-table td:last-child {
  padding-right: 0;
}

.tooltip-content .tooltip-table tr:last-child td {
  padding-bottom: 0;
}

.tooltip-content .tooltip-table th:first-child {
  text-align: left;
  padding-left: 0;
}

.tooltip-content .tooltip-table th:last-child {
  padding-right: 0;
}

.tooltip-content .tooltip-table .row-header {
  text-align: left;
  width: 100px;
}

@media only screen and (width <= 680px) {
  .tippy-popper {
    width: 100%;
    max-width: none;
    margin: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: fixed !important;
    transform: none !important;
  }

  .tippy-tooltip {
    width: 100%;
    box-shadow: none;
    color: #fff;
    background-color: #000;
    border: none;
    border-top: 1px solid #fff;
    border-radius: 0;
    margin: 0;
    padding: 20px;
    position: absolute;
    bottom: 0;
    right: 0;
    max-width: none !important;
    top: auto !important;
    left: 0 !important;
  }

  .tooltip-content .tooltip-title {
    margin-bottom: 17px;
  }

  .tooltip-content .tooltip-table tbody tr {
    border-top: none;
  }

  .tooltip-content {
    font-size: 13px;
  }

  .tooltip-content .tooltip-table td:not(:first-child) {
    font-family: GT America Bold;
  }
}
